import React from "react";
import {
  Upload,
  Form,
  Input,
  Select,
  Button,
  InputNumber,
  notification,
} from "antd";
import { UploadOutlined } from "@ant-design/icons";
import { useNavigate } from "react-router-dom";
import { instance } from "../../utils/axios";

const layout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 16 },
};
const { Option } = Select;
const { TextArea } = Input;

export default function Exam() {

  const navigateHome = ()=>{
    navigate("/home")
  }


  const normFile = (e) => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };
  let navigate = useNavigate();
  const onFinish = (values) => {
    let files = values.files;
    let images = values.images;

    if (
      !(Array.isArray(files) && files.length > 0) &&
      !(Array.isArray(images) && images.length > 0)
    ) {
      notification["error"]({
        message: "Nhp t nht nh hoc file",
        placement: "top",
      });
      return;
    }

    let formData = new FormData();
    formData.append("title", values.title);
    formData.append("description", values.description);
    formData.append("school", values.school);
    formData.append("type", values.type);
    formData.append("class", values.class);
    formData.append("system", values.system);
    formData.append("semester", values.semester);
    formData.append("ship", values.ship);
    formData.append("price", values.price);

    if (Array.isArray(files) && files.length > 0) {
      for (const iterator of files) {
        formData.append("files", iterator.originFileObj);
      }
    }

    if (Array.isArray(images) && images.length > 0) {
      for (const iterator of images) {
        formData.append("images", iterator.originFileObj);
      }
    }

    instance
      .post("/apis/exam/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        if (res.data.isError) {
          notification["error"]({
            message: res.data.error.message,
            placement: "top",
          });

          navigate("/login");
        } else {
          notification["success"]({
            message: "Upload successfully",
            placement: "top",
          });
          navigate("/home");
        }
      })
      .catch((err) => {
        notification["error"]({
          message: "error",
          placement: "top",
        });
      });
  };

  return (
    <div>
      <Form {...layout} onFinish={onFinish}>
        <Form.Item
          name="title"
          label="Tiu "
          rules={[{ required: true, message: "This field is required!" }]}
        >
          <Input style={{ width: "100%" }} />
        </Form.Item>

        <Form.Item
          name="description"
          label="Miu t"
          rules={[{ required: true, message: "This field is required!" }]}
        >
          <TextArea rows={4} />
        </Form.Item>

        <Form.Item name="school" label="Trng">
          <Select
            showSearch
            placeholder="Chn trng"
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            allowClear
          >
            <Option value="Trng i hc Khoa hc T nhin - i hc Quc gia TP.HCM">
              Trng i hc Khoa hc T nhin - i hc Quc gia TP.HCM
            </Option>
            <Option value="Trng i hc Bch khoa - i hc Quc gia TP.HCM">
              Trng i hc Bch khoa - i hc Quc gia TP.HCM
            </Option>
            <Option value="Trng i hc Cng ngh Thng tin - HQG TP.HCM">
              Trng i hc Cng ngh Thng tin - HQG TP.HCM
            </Option>
          </Select>
        </Form.Item>

        <Form.Item name="class" label="Lp">
          <Input style={{ width: "100%" }} />
        </Form.Item>

        <Form.Item name="system" label="H" initialValue={"Chnh quy"}>
          <Select
            showSearch
            placeholder="Chn h"
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            allowClear
          >
            <Option value="Chnh quy">Chnh quy</Option>
            <Option value="Tin tin">Tin tin</Option>
            <Option value="Vit Php">Vit Php</Option>
            <Option value="Cao ng">Cao ng</Option>
            <Option value="Ti nng">Ti nng</Option>
            <Option value="Cht lng cao">Cht lng cao</Option>
          </Select>
        </Form.Item>

        <Form.Item name="semester" label="Hc k">
          <Select
            showSearch
            placeholder="Chn hc k"
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            allowClear
          >
            <Option value="Hc k 1">Hc k 1</Option>
            <Option value="Hc k 2">Hc k 2</Option>
            <Option value="Hc k 3 (H)">Hc k 3 (H)</Option>
          </Select>
        </Form.Item>

        <Form.Item name="ship" label="Ca">
          <InputNumber style={{ width: "100%" }} />
        </Form.Item>

        <Form.Item name="price" label="Gi">
          <Input style={{ width: "100%" }} />
        </Form.Item>

        <Form.Item name="files" label="PDF file" getValueFromEvent={normFile}>
          <Upload
            accept=".doc,.docx,.pdf"
            maxCount={8}
            beforeUpload={() => false}
          >
            <Button icon={<UploadOutlined />}>Click to Upload</Button>
          </Upload>
        </Form.Item>

        <Form.Item name="images" label="Images" getValueFromEvent={normFile}>
          <Upload accept="image/*" maxCount={8} beforeUpload={() => false}>
            <Button icon={<UploadOutlined />}>Click to Upload</Button>
          </Upload>
        </Form.Item>

        <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
          <Button onClick={navigateHome} style={{marginRight:10}}>Cancel</Button>

          <Button type="primary" htmlType="submit">
            Upload
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}

